<template>
  <div class="afterSaleCard">
    <van-pull-refresh v-model="isRefresh" @refresh="onRefresh">
      <van-list
          v-model="loading"
          :finished="finished"
          @load="onLoad(mgChildKey)"
          error-text="请求失败，刷新重新加载"
          :immediate-check="false"
          :style="{'min-height': (listMinHeight+'px')}"
      >
        <div class="orderList" v-for="(item ,i) in lists" v-if="isLists==false">
          <div class="commonShop">
            <div class="shopTitle" @click="toShop(item.shopCode)">
              <div class="shopName">{{item.shopName}}</div>
              <img :src="img.arrowShop">
            </div>
            <!--售后状态-->
            <div class="callShop" v-if="mgChildKey==1&&item.billStatus==0">待审核</div>
            <div class="callShop" v-if="mgChildKey==1&&item.billStatus==1">待退款</div>
            <div class="callShop" v-if="mgChildKey==2&&item.billStatus==0">退款成功</div>
            <div class="callShop" v-if="mgChildKey==2&&item.billStatus==1">申请未通过</div>

            <div class="normalCard" @click="showAfterDetail(item)">
              <img class="goodsPic" :src="item.spuPicUrl"/>
              <!--商品描述-->
              <div class="goodsDetail">
                <div class="spuName">{{item.spuName}}</div>
                <div class="spuAttr">{{item.attrValueContent}}</div>
                <div class="orderCount">x{{item.skuCount}}</div>
              </div>

              <div class="orderSC">
                <span class="redMoneyIcon">￥</span>
                <span class="redMoney">{{item.returnRealTotal}}</span>
              </div>
            </div>
            <!--<div class="orderDetail" >-->
            <!--<span class="redMoney">￥{{item.realPayTotal}}</span>-->
            <!--</div>-->
            <div class="bottomBtn" v-if="mgChildKey==0">
              <button class="turnToPay" @click="applyForAfter(item)">申请售后</button>
            </div>

          </div>
        </div>
        <!--无订单时的显示图片-->
        <div class="noList" v-if="isLists==true">
          <img :src="img.noList"/>
          <div>暂无订单</div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<style scoped="scoped" lang="scss">
</style>

<script>
  import {getAfterSaleList} from "../../services/orderListService";
  import {mapActions, mapState} from "vuex";
  import Vue from 'vue';

  export default {
    name: 'afterSaleCard',
    props: ['mgChildKey', 'isActKey', 'isClickEven'],
    data: function () {
      return {
        lists: [],
        isLists: false,
        img: {
          arrowShop: require("@/assets/images/mall/mall_icon_more.png"),
          callShop: require("@/assets/images/mall/call_small.png"),
          noList: require("@/assets/images/default/no_after_sales_img.png"),//img里面没有找到
        },
        loading: false,//上拉加载状态
        finished: false,//上拉加载无数据时的状态
        page: 1,//当前接口请求的页数
        totalPages: 5,//订单列表的总页数，先设置一个大于page的初始值
        isRefresh: false,//是否处于刷新状态
        listMinHeight: document.body.clientHeight - this.$stbHeight - 55 - 44,
        ifDetailReturn: false,
        /*大字模式*/
        bindThemeL: this.$curTheme == 0 ? false : true
      }
    },
    created() {
      if (this.$deciceType=='phone'){
        require('../../assets/css/phone/myOrderList/afterSaleCard.scss')
      }else if (this.$deciceType=='portraitPad'){
        require('../../assets/css/portraitPad/myOrderList/afterSaleCard.scss')
      }
    },
    methods: {
      // 下拉刷新
      onRefresh(type) {
        this.page = 1;
        this.returnOrderList(type);
        this.finished = false;
      },
      //清空数据
      emptyData() {
        var arr = [];
        arr.push(this.lists[0])
        this.lists = [];
      },
      //退货列表查询
      returnOrderList(type) {
        const _this = this;

        const data = {
          page: _this.page,
          row: 5,
          // ownerCode: '2019110817314234128',
          ownerCode: _this.$userInfo.OWNER_CODE,
          returnStatus: _this.mgChildKey,
        }
        getAfterSaleList(data).then(function (response) {
          _this.totalPages = response.pages;
          if (_this.page == 1) { //如果是下拉刷新，从第一页开始刷新
            _this.lists = [];
          }
          if (response.total == 0) {
            _this.isLists = true;
            _this.finished = false;
            _this.loading = false;
            _this.isRefresh = false;
          } else {
            _this.lists = _this.lists.concat(response.data);
            _this.lists.map((item, index) => {
              //图片本地化存储
              item.picUrl = item.spuPicUrl;
              Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                item.spuPicUrl = localImgUrl;
              })
            });
            _this.page += 1;
            _this.loading = false;
            _this.finished = _this.page > _this.totalPage ? true : false;
            _this.isRefresh = false;
          }
        });
      },
      onLoad(type) {
        const _this = this;
        if (_this.mgChildKey != _this.isActKey) {
          return
        }
        console.log("初始化的时候走了这里");
        if (_this.totalPages >= _this.page) {
          _this.returnOrderList(type);
        } else if (_this.totalPages < _this.pageisLists || _this.lists != '') {
          _this.finished = true;
        }
      },
      // fresh(){
      //   const _this=this;
      //   if(_this.isRefresh==true){
      //     _this.returnOrderList();
      //     _this.finished=false;
      //   }
      // },
      //申请售后跳转按钮
      applyForAfter(item) {
        if (!this.isClickEven) {
          return;
        }
        this.$router.push({name: 'afterSaleApply', params: {detail: item}});
      },
      //售后详情
      showAfterDetail(item) {
        if (!this.isClickEven) {
          return;
        }
        if (this.mgChildKey != 0) {
          this.$router.push({name: 'afterSaleDetail', params: {detail: item, flag: this.mgChildKey}});
        }
      },
      //跳转店铺首页
      toShop(shopCode) {
        if (!this.isClickEven) {
          return;
        }
        this.$router.push({name: 'shopPage', params: {shopCode: shopCode}});
      },
    },

  }
</script>
